@import url('https://fonts.googleapis.com/css2?family=Coustard&family=Josefin+Sans:ital,wght@1,300;1,400&family=Merriweather:wght@300&display=swap');

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
body {
  background-color: rgb(255, 255, 255);
}
.contact {
  color: rgb(47, 11, 192);
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 3vw;
  font-family: 'Coustard', serif;
  font-family: 'Josefin Sans', sans-serif;
  font-family: 'Merriweather', serif;
}
a {
  font-size: 2rem;
}
.a-instagram {
  color: rgb(254, 3, 233);
}
.a-github {
  color: black;
}
.a-linkedin {
  color: rgb(17, 7, 201);
}
.a-gmail {
  color: rgb(219, 85, 85);
}
.profile {
  font-family: 'Coustard', serif;
  font-family: 'Josefin Sans', sans-serif;
  font-family: 'Merriweather', serif;
  display: flex;
}
.intro {
  font-size: 1rem;
  margin-left: 3vw;
}
.skill {
  width: 30%;
  border-radius: 4px;
}
.html {
  width: 50px;
  height: 70px;
}
.css {
  width: 50px;
  height: 70px;
}
.js {
  width: 70px;
  height: 70px;
}
.react {
  width: 50px;
  height: 70px;
}
.nodejs {
  width: 50px;
  height: 70px;
}
.expressjs {
  width: 70px;
  height: 80px;
}
.mongodb {
  width: 70px;
  height: 70px;
}
.firebase {
  width: 50px;
  height: 70px;
}
.git {
  width: 200px;
  height: 80px;
}
.skill h2 {
  font-size: 2rem;
  font-family: 'Coustard', serif;
  font-family: 'Josefin Sans', sans-serif;
  font-family: 'Merriweather', serif;
}
.links {
  position: absolute;
  top: 50%;
  right: 0%;
  font-size: 1rem;
  font-family: 'Coustard', serif;
  font-family: 'Josefin Sans', sans-serif;
  font-family: 'Merriweather', serif;
}
.links a {
  font-size: 1.5rem;
  display: block;
}

@media screen and (max-width: 450px) {
  .links {
    position: relative;
    top: 0;
    right: 0;
  }
  .links a {
    font-size: 1.5rem;
  }
}

@media screen and (min-width: 460px) and (max-width: 950px) {
  .links {
    position: relative;
    left: 0;
  }
}